@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&family=Oswald:wght@600;700&display=swap);

* { 
    margin: 0; 
    padding: 0; 
    border: 0; 
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}


.app{
    position: relative;
    height: auto;
}
.xs{font-size:x-small}.sm{font-size:small}.md{font-size:medium}.lg{font-size:large}.xl{font-size:x-large}

.home-container{height:100%;display:block;justify-content:center;background-color:#272a35}.home-container .section-1{height:100vh;padding-top:6rem;display:flex;align-items:center;width:100%}.home-container .section-1 .caption-container{width:40%;display:flex;justify-content:center;align-items:center;margin-left:auto;height:70%;display:flex}.home-container .section-1 .caption-container .caption *{margin:0.8rem 0}.home-container .section-1 .caption-container .caption h1{color:#e8eaee;font-size:xxx-large;font-weight:500}.home-container .section-1 .caption-container .caption h6{color:#8790a2;font-size:small;font-weight:300}.home-container .section-1 .caption-container .caption .scroll-arrow-down{border:thin solid #8790a2;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding:5px 20px;border-radius:3rem;margin-top:3rem}.home-container .section-1 .caption-container .caption .scroll-arrow-down i{color:"#ffffff"}.home-container .section-1 div:nth-child(2n){display:flex;justify-content:center;align-items:center;margin-right:auto;height:70%;width:40%}.home-container .section-1 div:nth-child(2n) div{width:100%}.home-container .section-2{height:100%;padding-top:2rem;color:#fff}.home-container .section-2 .crypto-highlights{padding-top:5rem;padding-left:3rem;padding-right:3rem;padding-bottom:10rem;background-color:black}.home-container .section-2 .crypto-highlights h1{color:#e8eaee;font-size:xx-large;font-weight:500;left:auto;text-align:center}.home-container .section-2 .crypto-highlights p{margin-bottom:2rem;color:#8790a2;text-align:center}.home-container .section-2 .highlights-grp{display:flex;justify-content:space-between;margin-top:3rem}.home-container .section-2 .get-started{background-color:#141416;padding-top:30vh;padding-bottom:20vh}.home-container .section-2 .get-started h1{text-align:center;font-weight:500}.home-container .section-2 .get-started p{color:#8790a2;text-align:center;margin-bottom:2rem}.home-container .section-2 .get-started .get-started-tab-grp{margin-top:3rem;display:flex;align-items:center;margin-left:auto;margin-right:auto;width:80%;justify-content:space-around;padding-bottom:2rem}.home-container .section-2 .get-started .get-started-tab-grp hr{color:#fff;width:auto;min-width:100px;height:-webkit-min-content;height:min-content;background-color:#8790a2;border:1px solid #8790a2}

.crypto-card{background-color:#23262f;min-width:250px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;min-height:200px;border-radius:15px;padding:0.5rem;display:flex;flex-direction:column;color:whitesmoke;font-weight:400}.crypto-card .crypto-card-header-section{width:100%;justify-content:space-between;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;display:flex;align-items:center}.crypto-card .crypto-card-header-section img{position:relative;width:6%;min-width:50px;height:auto;height:50px}.crypto-card .cryto-card-info-block{padding:1rem}

.stepperbtn{display:flex;align-items:center;justify-content:space-between;max-width:-webkit-max-content;max-width:max-content;box-shadow:rgba(255,255,255,0.25) 0px 30px 60px -12px inset,rgba(255,255,255,0.3) 0px 18px 36px -18px inset;border-radius:2rem;padding:15px 32px}.stepperbtn label{margin-left:1rem}

.table{width:100%}.table .table-head{background-color:#17181b;width:100%;display:block;color:#8790a2;display:flex;justify-content:space-evenly}.table .table-head th{display:flex}.table .table-head th .t-filter{margin-left:4px}.arrow-up,.arrow-down{font-weight:700;font-family:'Oswald', sans-serif;transform:rotate(90deg)}.updownarrow{position:relative;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;background-color:red;display:flex;flex-direction:column;justify-content:center;align-items:center;margin:0;padding:0}.updownarrow .arrow-up{position:absolute}.updownarrow .arrow-down{position:absolute}

.c-navbar{z-index:5;position:-webkit-sticky;position:sticky;top:0;height:64px;background-image:linear-gradient(to right, #141416, #272a35);width:100%;display:flex;align-items:center}.c-navbar .c-nav-logo{color:#FFFFFF;padding:15px 32px;background-color:transparent}.c-nav-group{color:#8790a2;font-size:smaller;display:flex}.c-nav-group li{list-style:none;padding:15px 32px;cursor:pointer}.c-nav-btn-group{margin-left:auto;width:20%;display:flex;justify-content:space-around}.c-nav-menu{background-color:transparent;color:#e8eaee;border:none;outline:none}


.footer .contact-us{padding-left:3rem;padding-top:5rem;display:flex;align-items:center;height:-webkit-max-content;height:max-content;background-color:#272a35;opacity:9;background-size:contain;width:100%}.footer .contact-us h1{color:#e8eaee;font-weight:300}.footer .contact-us p{color:#e8eaee;margin-bottom:3rem}.footer .contact-us input{font-size:large;width:-webkit-max-content;width:max-content;padding:15px 15px;border-radius:1rem;margin-right:1rem}

.custom-underline{width:10rem;background:linear-gradient(#3d5afe, #8c9eff);height:5px;border-radius:5rem;margin-top:1rem;margin-bottom:2rem;margin-left:auto;margin-right:auto}

